document.addEventListener('DOMContentLoaded', () => {
    const games = [
      { id: '1', title: 'Game 1', price: 30, image: 'path/to/image1.jpg', description: 'Description of Game 1', discount: 10 },
      { id: '2', title: 'Game 2', price: 40, image: 'path/to/image2.jpg', description: 'Description of Game 2', discount: 0 },
      { id: '3', title: 'Game 3', price: 50, image: 'path/to/image3.jpg', description: 'Description of Game 3', discount: 20 },
      { id: '4', title: 'Game 4', price: 0, image: 'path/to/image4.jpg', description: 'Description of Game 4', discount: 0 },
    ];
  
    const renderGames = (gamesList) => {
      const gamesListElement = document.getElementById('gamesList');
      gamesListElement.innerHTML = '';
      gamesList.forEach(game => {
        const gameCard = document.createElement('div');
        gameCard.className = 'game-card';
        gameCard.innerHTML = `
          <img src="${game.image}" alt="${game.title}" class="game-image">
          <h2>${game.title}</h2>
          <p>Price: $${game.price}</p>
          <a href="#" class="related-articles" data-id="${game.id}">Related articles</a>
          <button class="buy-button" data-id="${game.id}">Buy</button>
        `;
        gamesListElement.appendChild(gameCard);
      });
    };
  
    const renderPromotions = (gamesList) => {
      const promotionsListElement = document.getElementById('promotionsList');
      promotionsListElement.innerHTML = '';
      gamesList.forEach(game => {
        if (game.price === 0 || game.discount > 0) {
          const promotionCard = document.createElement('div');
          promotionCard.className = 'promotion-card';
          promotionCard.innerHTML = `
            <img src="${game.image}" alt="${game.title}" class="promotion-image">
            <h3>${game.title}</h3>
            ${game.price === 0 ? `<p class="free">Gratuit</p>` : `<p class="discount">Prix r&eacute;duit: $${game.price} (Economisez $${game.discount})</p>`}
            <button class="buy-button" data-id="${game.id}">Buy</button>
          `;
          promotionsListElement.appendChild(promotionCard);
        }
      });
    };
  
    const handleBuyButtonClick = (event) => {
      if (event.target.classList.contains('buy-button')) {
        const gameId = event.target.getAttribute('data-id');
        const game = games.find(g => g.id === gameId);
        showCheckout(game);
      }
    };
  
    const showCheckout = (game) => {
      const checkoutSection = document.getElementById('checkoutSection');
      const checkoutSteps = document.getElementById('checkoutSteps');
      checkoutSteps.innerHTML = `
        <div class="checkout">
          <h2>1. Game</h2>
          <div>
            <p>Title: ${game.title}</p>
            <p>Original Price: $${game.price}</p>
            <p>Discount: $${game.discount}</p>
            <p>Final Price: $${game.price - game.discount}</p>
          </div>
          <button class="next-button" onclick="showCreditCardStep()">Next</button>
        </div>
      `;
      checkoutSection.style.display = 'block';
    };
  
    window.showCreditCardStep = () => {
      const checkoutSteps = document.getElementById('checkoutSteps');
      checkoutSteps.innerHTML = `
        <div class="checkout">
          <h2>2. Credit Card</h2>
          <div>
            <p>Enter your credit card details:</p>
            <input type="text" placeholder="Card Number">
            <input type="text" placeholder="Name on Card">
            <input type="text" placeholder="Expiry Date">
            <input type="text" placeholder="CVV">
          </div>
          <button class="next-button" onclick="showConfirmationStep()">Next</button>
        </div>
      `;
    };
  
    window.showConfirmationStep = () => {
      const checkoutSteps = document.getElementById('checkoutSteps');
      checkoutSteps.innerHTML = `
        <div class="checkout">
          <h2>3. Confirmation</h2>
          <p>Review your details and confirm your purchase.</p>
          <button class="confirm-button" onclick="confirmPurchase()">Confirm</button>
        </div>
      `;
    };
  
    window.confirmPurchase = () => {
      alert('Purchase confirmed!');
      const checkoutSection = document.getElementById('checkoutSection');
      checkoutSection.style.display = 'none';
    };
  
    document.getElementById('gamesList').addEventListener('click', handleBuyButtonClick);
    document.getElementById('promotionsList').addEventListener('click', handleBuyButtonClick);
  
    renderGames(games);
    renderPromotions(games);
  });
  